body{
    height: fit-content;
}
.row:first-child{
    margin-bottom: 10px;
}
.title{
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 3px;
}
.row:nth-child(2){
    display: flex;
}
.row:nth-child(2) .col{
    padding: 0 15px;
}
.row:nth-child(2) .col:first-child{
    padding-top: 200px;
    flex: 1;
    display: flex;
    flex-direction: column;
}

.row:nth-child(2) .col:last-child{
    flex:9;
}

button{
    margin-bottom: 50px;
    font-size: 17px;
    background-color: transparent;
    border: none;
    width: max-content;
    outline: none;
    text-transform: uppercase;
    transition: all 0.5s;
    cursor: pointer;
}
button:hover , button:focus{
    font-weight: bold;
    color: #75C0F3;
}
:root {
    --surface-color: #fff;
    --curve: 40;
  }
  
.cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    margin: 4rem 5vw;
    padding: 0;
    list-style-type: none;
  }
  
  .card {
    position: relative;
    display: block;
    height: 100%;  
    border-radius: calc(20 * 1px);
    overflow: hidden;
    text-decoration: none;
    /* border: 1px solid ; */
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  }
  
  .card__image {      
    width: 70%;
    height: auto;
    margin-left: 55px;
  }
  
  .card__overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;      
    border-radius: calc(var(--curve) * 1px);    
    background-color: var(--surface-color);      
    transform: translateY(100%);
    transition: .2s ease-in-out;
  }
  
  .card:hover .card__overlay {
    transform: translateY(0);
  }
  
  .card__header {
    position: relative;
    display: flex;
    align-items: center;
    gap: 2em;
    padding: 2em;
    border-radius: calc(var(--curve) * 1px) 0 0 0;    
    background-color: var(--surface-color);
    transform: translateY(-100%);
    transition: .2s ease-in-out;
  }
  
  .card__arc {
    width: 80px;
    height: 80px;
    position: absolute;
    bottom: 100%;
    right: 0;      
    z-index: 1;
  }
  
  .card__arc path {
    fill: var(--surface-color);
    d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
  }       
  
  .card:hover .card__header {
    transform: translateY(0);
  }
  
  .card__thumb {
    flex-shrink: 0;
    width: 50px;
    height: 50px;      
    border-radius: 50%;      
  }
  
  .card__title {
    font-size: 1em;
    margin: 0 0 .3em;
    color: #6A515E;
  }
  
  .card__tagline {
    display: block;
    margin: 1em 0;
    font-family: "MockFlowFont";  
    font-size: .8em; 
    color: #D7BDCA;  
  }
  
  .card__status {
    font-size: .8em;
    color: #D7BDCA;
  }
  
  .card__description {
    padding: 0 2em 2em;
    margin: 0;
    color: #D7BDCA;
    font-family: "MockFlowFont";   
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }    